import {post,get,toLogin} from '@/common/util.js';
import tabbar from '@/components/tabbar.vue';
import calendar from '@/components/date-picker/date-picker';



import wpicker from "@/components/w-picker/w-picker.vue";
export default {
	data() {
		return {
			userId: "",
			token: "",
			currentSwiper :0,
			showCaledar: false,
			dateStr: '',
			daysCount: 130,
			singleDate: true,
			
			//初始日期
			initStartDate: '2019-12-06',
			initEndDate: '2019-12-07',
			showCoupon:false,
			cityname:"",//定位城市
			nowCity:"",//当前城市
			AreaCode:"",//区域国家码
			AreaType:0,//1不限市，区
			classifyDefault:'深圳华侨城5A级景区',
			classifyList:[
					{
						label:"深圳华侨城5A级景区",
						value:"1",
					},
					{
						label:"深圳华小梅沙度假区",
						value:"2",
					},
					{
						label:"深圳华梧桐山景区",
						value:"3",
					}
				],
		}
	},
	components: {
		tabbar,
		calendar,
		wpicker
	},
	onLoad() {
		this.userId = uni.getStorageSync("userId");
		this.token = uni.getStorageSync("token");
		var _this=this

		uni.getLocation({
		    type: 'wgs84',
			geocode: true,
		    success: function (res) {console.log(res)









				_this.wxGetCity(res.longitude,res.latitude)

		        // console.log(res);
		    }
		});

		//百度定位













	},
	onShow(){
		this.cityname=uni.getStorageSync("cityname");
		this.getAreaCode(this.cityname);
	},
	onBackPress() {
		if (this.showCaledar !== false) {
			this.showCaledar = false;
			return true;
		}
	},
	methods: {
		scan() {
			uni.scanCode({
				success:function(res){
					console.log(JSON.stringify(res));
				}
			});
		},
		//跳转
		tolink(Url,islogin) {
			if(islogin=="login"){
				if(toLogin()){
					uni.navigateTo({
						url: Url
					})
				}
			}else{
				uni.navigateTo({
					url: Url
				})
			}
		},
		changeSwiper(e){
			this.currentSwiper=e.detail.current;
		},
		change({
			choiceDate,
			dayCount
		}) {
			//参数解释
			//1.choiceDate 时间区间（开始时间和结束时间）
			//2.dayCount 共多少晚
		
			// console.log(choiceDate, dayCount);
			console.log('入住从 ' + choiceDate[0].re + '  到 ' + choiceDate[1].re + '  共 ' + dayCount + ' 晚');
		},
		hideCoupon(){
			this.showCoupon=false;
		},
		// 定位当前城市
		getlocationNow(){
			this.cityname=this.nowCity;
			this.getAreaCode(this.nowCity);
		},
		//小程序解析经纬度获取城市

		wxGetCity(lon,lat){
			var _this=this
			wx.request({
				url:'https://api.map.baidu.com/reverse_geocoding/v3/?ak=3wwDKCk09o6hU0PK1605QUXOCBqGVHGx&location=' + lat + ',' + lon + '&output=json&coordtype=wgs84ll',
				data: {},
				header: {
					'content-type': 'application/json' // 默认值
				},
				success (res) {
					 console.log("res")
				     console.log(res)
					var cityname=res.data.result.addressComponent.city.replace(/市/,'')
					uni.setStorageSync('cityname',cityname)
					_this.cityname=cityname
					_this.getAreaCode(cityname)
				}
			})
		},

		async getAreaCode(name) {
			if(name&&name!='全国'){
				let result = await post("Area/GetCityCode", {
					Name:name
				});
				if (result.code === 0) {
					this.AreaCode = result.data.Code;
					this.AreaType = 1;
				}
			}else if(name=='全国'){
				this.AreaCode = "";
				this.AreaType = 0;
			}else{
				this.AreaCode = "";
				this.AreaType = 0;
			}
			uni.setStorageSync('AreaCode',this.AreaCode);
		},
		// 选择分类
		onClassify(){
			this.$refs['selector'].show();
		},
		pickerclassOk(e){
			this.classifyDefault=e.result;
		},
	},













}
